.headerContainer {
    height: 72px;
    box-shadow: -1px -3px 11px 0px rgb(0 0 0 / 35%);
    padding: 0 20px;
    display: flex;
        align-items: center;
        justify-content: center;
}

 .header {
     width: 100%;
    display: flex;
        align-items: center;
        justify-content: space-between;
 }

 .logoContainer{
     margin-left: 10px;
     height: 60px;
     width: 60px;
     background-image: url(tested.png);
     background-position: center;
     background-repeat: no-repeat;
     background-size: cover;
     flex-shrink: 0;
 }

 .linksContainer {
     width: 100%;
     display: flex;
 }

 .Link {
     margin: 10px;
     text-decoration: none;
     color: rgb(160, 160, 160);
 }

 .Linkactive {
     color: black;
     -webkit-text-stroke: 0.5px black;
 }



